/**
 * Extrait le numro de l'quipe  partir du nom de l'quipe.
 * Si le numro n'est pas trouvable, retourne un fallback.
 * 
 * @param {string} teamName - Le nom de l'quipe (par exemple "Team 40").
 * @param {string|number} fallback - La valeur de retour par dfaut si aucun numro n'est trouv.
 * @returns {string|number} Le numro extrait ou le fallback.
 */
export function extractTeamNumber(teamName, fallback = 'Unknown') {
    if (!teamName || typeof teamName !== 'string') {
        return fallback;
    }

    const parts = teamName.split(' ');
    return parts.length > 1 && !isNaN(parts[1]) ? parts[1] : fallback;
}
