// src/router/tourneyGuard.js

import store from '@/store';
import { isAdmin } from '@/services/authService';
import apiService from '@/services/apiService';

export async function checkTournamentAccess(to, from, next) {
  const isOffline = !navigator.onLine;
  const userRole = store.getters['auth/userRole'];
  const tourneyId = to.params.tourneyId;

  if (isAdmin(userRole)) {
    return next(); // Les admins ont accs libre
  }

  // Si hors ligne
  if (isOffline) {
    // Vrifier si on a des donnes en cache pour ce tournoi
    const cachedData = localStorage.getItem(`planning-${tourneyId}`);
    if (cachedData) {
      // On a des donnes locales, on autorise la navigation
      return next();
    } else {
      // Pas de donnes, retour  /tourneys
      return next('/tourneys');
    }
  }

  try {
    // Vrifie si l'utilisateur est associ au tournoi
    const response = await apiService.get(
      `/users/me/tourneys/${tourneyId}/role`
    );
    const userRoleInTourney = response.data.tourneyRole;
    if (userRoleInTourney) {
      // Stocke le rle dans le store si ncessaire
      store.commit('userTourney/SET_TOURNEY_ROLE', userRoleInTourney);
      return next();
    } else {
      // Si l'utilisateur essaie dj d'accder  '/tourneys', laisser passer
      if (to.path === '/tourneys') {
        return next();
      }
      // Sinon, rediriger vers la liste des tournois
      return next('/tourneys');
    }
  } catch (error) {
    console.error(
      "Erreur lors de la vrification de l'accs au tournoi:",
      error
    );
    // Si l'utilisateur essaie dj d'accder  '/tourneys', laisser passer
    if (to.path === '/tourneys') {
      return next();
    }
    return next('/tourneys'); // Redirection en cas d'erreur
  }
}

export async function checkTourneyRules(to, from, next) {
  const isOffline = !navigator.onLine;
  const userRole = store.getters['auth/userRole'];
  const tourneyId = to.params.tourneyId;

  if (isAdmin(userRole)) {
    return next(); // Les admins ont toujours accs
  }

  // Si hors ligne
  if (isOffline) {
    // Vrifier si on a des donnes en cache pour ce tournoi
    const cachedData = localStorage.getItem(`planning-${tourneyId}`);
    if (cachedData) {
      // On a des donnes locales, on autorise la navigation
      return next();
    } else {
      // Pas de donnes, retour  /tourneys
      return next('/tourneys');
    }
  }

  try {
    // Charger les donnes du tournoi depuis le store
    const tourney = await store.dispatch(
      'userTourney/fetchTourneyById',
      tourneyId
    );

    if (!tourney) {
      // Redirection vers la liste des tournois si le tournoi n'existe pas
      return next('/tourneys');
    }

    // Conditions pour accder aux diffrentes pages
    const isTourneyActiveOrCompleted = ['active', 'completed'].includes(
      tourney.status
    );
    const isRegistrationActiveOrCompleted = ['active', 'completed'].includes(
      tourney.registrationStatus
    );

    // Vrification des permissions en fonction du statut du tournoi
    if (isTourneyActiveOrCompleted) {
      return next(); // Accs total si le tournoi est actif ou termin
    }

    if (
      ['draft', 'ready'].includes(tourney.status) &&
      isRegistrationActiveOrCompleted
    ) {
      // Accs restreint si le tournoi est en mode "draft" ou "ready" mais l'inscription est active ou termine
      const restrictedPaths = [
        `/tourneys/${tourneyId}/join-team`,
        `/tourneys/${tourneyId}/teams/${to.params.teamId}/details`,
      ];

      if (restrictedPaths.includes(to.path)) {
        return next(); // Accs autoris pour ces pages spcifiques
      }

      // Redirection vers la liste des tournois au lieu de 'access-denied'
      return next('/tourneys');
    }

    // Par dfaut, redirection vers la liste des tournois
    return next('/tourneys');
  } catch (error) {
    console.error(
      'Erreur lors de la vrification des rgles du tournoi :',
      error
    );
    return next('/tourneys'); // Redirection en cas d'erreur
  }
}
