<!-- components/StrategyPattern/Pool/strategies/CustomRoundRobinStrategy.vue -->
<template>
  <div>
    <p class="mb-4 text-light-form-text dark:text-dark-form-text">
      <span class="text-light-form-error dark:text-dark-form-error font-bold">
        <strong>Attention :</strong> Les pools existantes et les plannings
        associs seront supprims. Il est prfrable d'effectuer cette action
        une fois les inscriptions termines. </span
      ><br />
      Cet algorithme (custom round robin) va gnrer des pools et assigner les
      quipes du tournoi aux pools de manire optimise, en prenant en compte le
      nombre de terrains disponibles ainsi que les rglages.
      <strong
        >Il ne va jamais gnrer plus de Pools qu'il n'y a de terrains</strong
      >.<br />
    </p>
  </div>
</template>

<script>
  import apiService from '@/services/apiService';
  import { toast } from 'vue3-toastify';

  export default {
    props: {
      tourneyId: {
        type: String,
        required: true,
      },
      teams: {
        type: Array,
        required: true,
      },
      pools: {
        type: Array,
        required: true,
      },
      availableFields: {
        type: Number,
        required: true,
      },
    },
    methods: {
      async generatePools() {
        try {
          const response = await apiService.post(
            `/tourneys/${this.tourneyId}/pools/generate`
          );

          const { pools, teamsWithoutPool } = response.data;
          const poolCount = pools.length;
          const teamsWithoutPoolCount = teamsWithoutPool.length;

          toast.success(
            `Pools gnres avec succs ! ${poolCount} pools ont t cres.`,
            {
              autoClose: 3000,
            }
          );
          if (teamsWithoutPoolCount > 0) {
            toast.warning(
              `${teamsWithoutPoolCount} quipes n'ont pas pu tre assignes aux Pools.`,
              {
                autoClose: 4000,
              }
            );
          }
          return true;
        } catch (error) {
          console.error('Erreur lors de la gnration des pools :', error);
          const errorMessage =
            error.response?.data?.message ||
            'Erreur lors de la gnration des pools.';
          toast.error(errorMessage);
          return false;
        }
      },
    },
  };
</script>
